header {
    background: #000;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
}


.slideshow {
    opacity: 0.3;
    z-index: 1;
    position: absolute;
    width: 100%;
    height: 100%;
    
}

header .logo {
    z-index: 5;
    width: 62%;
    height: 62%;
}


h2 {
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-transform: uppercase;
    color: rgb(17, 17, 17);
    font-size: 3em;
}

#container {
    
}
.partidas *, #equipes *{
    transition: all 0.2s ease-in;
}
.partidas, #equipes {
    border-bottom: 1px solid #ccc;
    margin: 0 auto;
    margin-top: 40px;
    max-width: 1100px;
    position: relative;
    
    
    
}

.partidas div {
    display: inline-block;
    padding: 20px 50px;
    align-content: center;
}

.partidas div:hover, #equipes div:hover {
    transform: scale(1.2);
}

.partidas div:hover img {
    filter: drop-shadow( 3px 3px 4px rgba(0, 0, 0, .7));
}

.partidas div h3 {
    color: rgb(19, 19, 19);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 2em;
    text-align: center;
}

.partidas img {
    width: 230px;
    height: 230px;
}

.hide {
    animation: slide-out 1s forwards;
    position: relative;
    bottom: 0em;
    
}

.show {
    animation: slide-in 1s forwards;
    position: relative;
    bottom: -50em;

}

.partidas>section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
    margin-top: 3em;
}

@keyframes msg-in {
    100% {
        margin-top: 10em
    }
}

@keyframes slide-in {
    100% {
        bottom: 0em
    }
}

@keyframes slide-out {
    100% {
        bottom: -50em
    }
}
@keyframes slidenav-in {
    0% {
        top: -50em;
    }
    100% {
        top: 0em;
    }
}

@keyframes slidenav-out {
    100% {
        top: -50em
    }
}
nav{
    
    background: #fff;
    width: 100%;
    display: flex;
    justify-content: space-between;
    z-index: 10;
}
.showNav{
    position: fixed;
    animation: slidenav-in 1s forwards;
    box-shadow: 0px 5px 40px 2px rgba(0, 0, 0, .4);
    
}
.hideNav{
    position: fixed;
    top: 0;
    left: 0;
    animation: slidenav-out 1s forwards;
}

nav ul {
    margin-right: 10px;
    
}
nav img{
    height: 65px;
    justify-content: flex-start;
    margin-top: 7px;
    margin-left: 20px;
    
}
.navbar-nomargin{
    margin: 0;
}
nav ul li {
    display: inline-block;
    padding: 29px 0px;
    
    text-decoration: none;
    
}

nav ul li .linkstyle{
    
    color: rgb(0, 0, 0);
    text-decoration: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 29px;
    font-size: 1.2em;
    
}

nav ul li#ativo {
    border-bottom: 7px solid rgb(33, 228, 82);
}
.jogosModal{
    background: rgb(33, 228, 82);
    width: 100%;
    height: 100%;
    

    
}
.iconeFechar{
    margin: 56px;
    width: 5%;
    float: left;
    
}
.iconeFechar img{
    padding: 10px;
    width: 65px;
    
}

#tabela{
    height: 37em;
    border-bottom: 1px solid #ccc;
}
#equipes > section{
    margin: 5em 0;
}
#equipes > section div{
    display: inline-block;
    margin: 2em;
}
#equipes h3{
    color: rgb(19, 19, 19);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 2em;
    text-align: center;
    padding-top: 1em;
}
#equipes img{
    width: 15em;
}
footer{
    margin-top: 5em;
    height: 5em;
    background: rgb(33, 228, 82);
}
footer h3{
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: normal;
    text-align: center;
    padding-top: 2em;
}
footer h3 a{
    text-decoration: none;
    color: #fff;
}
footer h3 a:hover{

    color: #fff;
}